import { AiOutlineClose, AiFillStar } from "react-icons/ai";
import React from "react";
import { BrowserRouter, Link, Route, Routes } from "react-router-dom";

const Auth = () => {
  const home1 = () => {
    const element = document.getElementById("change1");
    const element2 = document.getElementById("change2");

    document.getElementById("home1").addEventListener("click", () => {
      element.innerHTML = "Welcome to Tweerl";
    });
    document.getElementById("home1").addEventListener("click", () => {
      element2.innerHTML =
        "Tweerl is the easiest way for consumers to choose the right spot to eat and make plans with their friends and family. Finding new restaurants and choosing from among favorites can be challenging, so consumers use Tweerl to discover your restaurant and plan their night out. By claiming your Tweerl Business account, you can make sure you stand out, generating more traffic to your restaurant or eatery. You can edit your restaurant details and pictures, add or edit your menu, promote upcoming specials, and much more, all leading to more traffic through your restaurant. Access three different types of advertising, promoting your restaurant to appear on consumer screens more often, or at the beginning of their discovery to generate more awareness.Claiming and managing your Tweerl Business account is easy. Sign Up or Log In to get started!";
    });
  };

  const home2 = () => {
    const element = document.getElementById("change1");
    const element2 = document.getElementById("change2");

    document.getElementById("home2").addEventListener("click", () => {
      element.innerHTML = "How it Works";
    });
    document.getElementById("home1").addEventListener("click", () => {
      element2.innerHTML =
        "Tweerl’s efficient and fun experience makes it easy for everyone to get back into the flow of life and back into your restaurants. Our process promotes the discovery of your restaurants while eliminating the indecision that comes with planning a night out. Tweerl is a quick paced game that helps users discover new restaurants in their area and decide where to plan a night out. Draw your customers attention with eye-catching photos of your meals or attractive specials for the night. By claiming your business, you can determine what your customers see first when they find your custom restaurant card while deciding where to eat. Tweerl is the last stop for the customer and by customizing your business profile, you can ensure that your restaurant is chosen over others. Beyond profile customization, you can add other restaurant locations and upload menus. Access to customized advertisements to promote your restaurant, placing it at the front of the restaurant list, resulting in more swipes for your restaurant.Claim your free Business profile today and make your restaurant stand out.";
    });
  };
  return (
    <div className="main-bg h-[120vh] p-10">
      <div className="main-bg2 hidden md:flex flex-row h-[80vh] max-w-[1100px] rounded-2xl mx-auto">
        <div className=" bg-[white] w-[30%]  h-[100%] p-5 rounded-2xl">
          <div className=" flex flex-col justify-center items-center">
            <div className="m-3">
              <img src="data:image/png;base64,"></img>
            </div>

            <div className=" flex flex-col justify-center    w-[100%]  ">
              <div className="  flex float-left">
                <div
                  id="home1"
                  onClick={home1}
                  className="hover:bg-[greenyellow] w-[100%]  float-left  rounded hover:scale-105 ease-in-out duration-300 p-3 justify-center items-center my-2 flex flex-row gap-5"
                >
                  <AiFillStar className=" " />
                  <Link to="/Auth" className="home1">
                    Home
                  </Link>
                </div>
              </div>

              <div className="">
                <div
                  id="home2"
                  onClick={home2}
                  className="hover:bg-[greenyellow]  w-[100%]  float-left  rounded hover:scale-105 ease-in-out duration-300 p-3 justify-center items-center my-2 flex flex-row gap-5"
                >
                  <AiFillStar className="" />
                  <Link id="home2">How it Works</Link>
                </div>
              </div>

              <div className=" ">
                <div className="hover:bg-[greenyellow] w-[100%]  float-left  rounded hover:scale-105 ease-in-out duration-300 p-3 justify-center items-center my-2 flex flex-row gap-5">
                  <AiFillStar className="" />
                  <Link to="/Auth3">Log-in/Sign-Up</Link>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="bg-main2 bg-black">
          <div className="w-[50%]  absolute right-10 rounded-md translate-y-10  -translate-x-6 flex flex-col p-5 bg-white">
            <div className=" font-bold text-[25px]">
              <h1 className="change1" id="change1">
                Welcome to Tweerl
              </h1>
            </div>

            <div>
              <h1 id="change2" className="p-4">
                Tweerl is the easiest way for consumers to choose the right spot
                to eat and make plans with their friends and family. Finding new
                restaurants and choosing from among favorites can be
                challenging, so consumers use Tweerl to discover your restaurant
                and plan their night out. By claiming your Tweerl Business
                account, you can make sure you stand out, generating more
                traffic to your restaurant or eatery. You can edit your
                restaurant details and pictures, add or edit your menu, promote
                upcoming specials, and much more, all leading to more traffic
                through your restaurant. Access three different types of
                advertising, promoting your restaurant to appear on consumer
                screens more often, or at the beginning of their discovery to
                generate more awareness. Claiming and managing your Tweerl
                Business account is easy. Sign Up or Log In to get started!
              </h1>
            </div>

            <div className=" flex flex-row justify-center gap-5">
              <Link to="/Signup" className="main-bg p-3 rounded">
                Sign-Up
              </Link>
              <Link to="/Login" className="main-bg p-3 rounded">
                Login
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div>
        <div className="md:w-[30%] w-[100%] md:hidden justify-center items-center  md:absolute right-10 rounded-md  md:-translate-x-6 flex flex-col p-5 bg-white">
          <div className="m-3">
            <img src="data:image/png;base64,"></img>
          </div>
          <div className="  text-[black] font-semibold text-[14px]">
            <h1 className=" text-[#4fb93a] my-5 font-bold text-[25px] ">
              Welcome to Tweerl
            </h1>
            <h1 className=" my-3">Have any questions?</h1>
            <h1 className=" my-3">
              We’d love to hear what you want to see from Tweerl Business
              Portal.
            </h1>
            <h1 className=" my-3">Contact us at:</h1>

            <h1 className=" my-3">Info@tweerl.com</h1>
          </div>

          <div className=" flex flex-row justify-center gap-5">
            <Link to="/Signup" className="main-bg p-3 rounded">
              Sign-Up
            </Link>
            <Link to="/Login" className="main-bg p-3 rounded">
              Login
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Auth;
