import React from "react";
import { Link } from "react-router-dom";
const HowWork = () => {
  return (
    <div>
      <div className="  w-[40%]  absolute right-10 rounded-md translate-y-2 -translate-x-6 flex flex-col p-5 bg-white">
        <div className=" font-bold text-[#34b634] text-[25px]">
          <h1>How it Works</h1>
        </div>

        <div>
          <h1>
            Tweerl’s efficient and fun experience makes it easy for everyone to
            get back into the flow of life and back into your restaurants. Our
            process promotes the discovery of your restaurants while eliminating
            the indecision that comes with planning a night out. Tweerl is a
            quick paced game that helps users discover new restaurants in their
            area and decide where to plan a night out. Draw your customers
            attention with eye-catching photos of your meals or attractive
            specials for the night. By claiming your business, you can determine
            what your customers see first when they find your custom restaurant
            card while deciding where to eat. Tweerl is the last stop for the
            customer and by customizing your business profile, you can ensure
            that your restaurant is chosen over others. Beyond profile
            customization, you can add other restaurant locations and upload
            menus. Access to customized advertisements to promote your
            restaurant, placing it at the front of the restaurant list,
            resulting in more swipes for your restaurant. Claim your free
            Business profile today and make your restaurant stand out.
          </h1>
        </div>

        <div className=" flex flex-row justify-center gap-5">
          <Link to="/Signup" className="main-bg p-3 rounded">
            Sign-Up
          </Link>
          <Link to="/Login" className="main-bg p-3 rounded">
            Login
          </Link>
        </div>
      </div>
    </div>
  );
};

export default HowWork;
