import React, { useState, useEffect, useRef } from "react";

const Parllel = () => {
  const [scrollValue, setScrollValue] = useState(0);
  const containerRef = useRef(null);
  const isHovered = useRef(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrollValue(window.scrollY);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  useEffect(() => {
    const handleScrollEffect = () => {
      if (containerRef.current && isHovered.current) {
        const screenWidth = window.innerWidth;
        const imageWidth = containerRef.current.clientWidth;
        const maxTranslateX = (imageWidth - screenWidth) / 2;

        // Calculate the translation while preventing the image from going out of the display
        let translateX = (scrollValue - window.innerHeight / 2) / 2;
        translateX = Math.max(-maxTranslateX, Math.min(0, translateX));
        containerRef.current.style.transform = `translateX(${translateX}px)`;
      }
    };

    // Attach the scroll effect handler
    window.addEventListener("scroll", handleScrollEffect);

    return () => {
      // Clean up the event listener
      window.removeEventListener("scroll", handleScrollEffect);
    };
  }, [scrollValue]);

  const handleMouseEnter = () => {
    isHovered.current = true;
  };

  const handleMouseLeave = () => {
    isHovered.current = false;
  };

  const containerStyle = {
    width: "100%",
    height: "100px",
    transform: "rotate(180deg)",
    backgroundImage:
      "url(\"data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1440 170' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink'><defs><linearGradient id='sw-gradient-0' x1='0' x2='0' y1='1' y2='0'><stop stop-color='rgba(0, 0, 0, 1)' offset='0%' /><stop stop-color='rgba(0, 0, 0, 1)' offset='100%' /></linearGradient></defs><path style='transform:translate(1, 1px); opacity:1' fill='url(%23sw-gradient-0)' d='M0,68L205.7,85L411.4,68L617.1,68L822.9,34L1028.6,102L1234.3,136L1440,85L1645.7,85L1851.4,119L2057.1,0L2262.9,0L2468.6,68L2674.3,136L2880,85L3085.7,0L3291.4,102L3497.1,119L3702.9,0L3908.6,119L4114.3,51L4320,136L4525.7,17L4731.4,0L4937.1,34L4937.1,170L4731.4,170L4525.7,170L4320,170L4114.3,170L3908.6,170L3702.9,170L3497.1,170L3291.4,170L3085.7,170L2880,170L2674.3,170L2468.6,170L2262.9,170L2057.1,170L1851.4,170L1645.7,170L1440,170L1234.3,170L1028.6,170L822.9,170L617.1,170L411.4,170L205.7,170L0,170Z' /></svg>\")",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
  };

  const containerStyle2 = {
    width: "100%",
    height: "100px",
    transform: "rotate(0deg)",
    backgroundImage:
      "url(\"data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1440 170' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink'><defs><linearGradient id='sw-gradient-0' x1='0' x2='0' y1='1' y2='0'><stop stop-color='rgba(0, 0, 0, 1)' offset='0%' /><stop stop-color='rgba(0, 0, 0, 1)' offset='100%' /></linearGradient></defs><path style='transform:translate(1, 1px); opacity:1' fill='url(%23sw-gradient-0)' d='M0,68L205.7,85L411.4,68L617.1,68L822.9,34L1028.6,102L1234.3,136L1440,85L1645.7,85L1851.4,119L2057.1,0L2262.9,0L2468.6,68L2674.3,136L2880,85L3085.7,0L3291.4,102L3497.1,119L3702.9,0L3908.6,119L4114.3,51L4320,136L4525.7,17L4731.4,0L4937.1,34L4937.1,170L4731.4,170L4525.7,170L4320,170L4114.3,170L3908.6,170L3702.9,170L3497.1,170L3291.4,170L3085.7,170L2880,170L2674.3,170L2468.6,170L2262.9,170L2057.1,170L1851.4,170L1645.7,170L1440,170L1234.3,170L1028.6,170L822.9,170L617.1,170L411.4,170L205.7,170L0,170Z' /></svg>\")",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
  };

  return (
    <div className="main-bg flex flex-col ">
      <div className="background-container" style={containerStyle}></div>

      <div className=" md:flex text-[white] p-10 flex-row max-w-[1000px] mx-auto">
        <div className="w-[100%] md:w-[50%]   justify-center items-center flex flex-col ">
          <h1 className=" font-thin text-[40px]">
            “The next big social app isn’t going to be social media - it’s going
            to be about using technology to connect us in real life”
          </h1>

          <h1 className=" font-semibold text-[greenyellow] text-[30px]">
            - Arianna Huffington
          </h1>
        </div>

        <div
          className=" h-[60vh] scale-125  w-[100%]  md:w-[50%]"
          ref={containerRef}
          style={{
            display: "inline-block",
            whiteSpace: "nowrap",
            transition: "transform 3s",
            overflow: "hidden", // To hide the image when it moves out of the screen
          }}
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <img
            className="scale-110"
            style={{ height: "60vh" }}
            src="https://tweerl.com/wp-content/uploads/2021/10/iphone-xs-max-mockup-featuring-a-color-custom-backdrop-244-el-7-e1634137430467.png"
            alt="Phone"
          />
        </div>

        <div></div>
      </div>

      <div className="background-container" style={containerStyle2}></div>
    </div>
  );
};

export default Parllel;
