import React from "react";
import { Link } from "react-router-dom";

const Alllogin = () => {
  return (
    <div className="w-[30%]  absolute right-10 rounded-md  -translate-x-6 flex flex-col p-5 bg-white">
      <div className="  text-[black] font-semibold text-[14px]">
        <h1 className=" text-[#4fb93a] my-5 font-bold text-[25px] ">
          Welcome to Tweerl
        </h1>
        <h1 className=" my-3">Have any questions?</h1>
        <h1 className=" my-3">
          We’d love to hear what you want to see from Tweerl Business Portal.
        </h1>
        <h1 className=" my-3">Contact us at:</h1>

        <h1 className=" my-3">Info@tweerl.com</h1>
      </div>

      <div className=" flex flex-row justify-center gap-5">
        <Link to="/Signup" className="main-bg p-3 rounded">
          Sign-Up
        </Link>
        <Link to="/Login" className="main-bg p-3 rounded">
          Login
        </Link>
      </div>
    </div>
  );
};

export default Alllogin;
