import React from "react";
import { AiOutlineClose, AiFillStar } from "react-icons/ai";
import { BrowserRouter, Link, Route, Routes } from "react-router-dom";

const Login = () => {
  return (
    <div>
      <div className=" h-[100vh]">
        <div className="md:flex flex-row">
          <div className="main-bg  w-[100%]  md:w-[40%]    h-[100vh] p-5 rounded">
            <div className=" flex flex-col justify-between items-center">
              <div className="m-3 ">
                <img src="data:image/png;base64,"></img>
              </div>

              <div className=" text-[30px] mt-24 flex text-[white] justify-center items-center font-bold ">
                <h1>Tweerl : Restaurant Web</h1>
              </div>

              <div className=" absolute bottom-2  font-bold text-[white]">
                <h1>&copy;2022 Tweerl</h1>
              </div>
            </div>
          </div>

          <div className=" text-[black] bg-[white] w-[100%]  md:w-[60%] h-[100vh] flex justify-center items-center ">
            <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
              <div className="sm:mx-auto sm:w-full sm:max-w-sm">
                <img
                  className="mx-auto h-10 w-auto"
                  src="https://tweerl.com/wp-content/uploads/2021/03/cropped-tweerl.logo_.png"
                  alt="Your Company"
                />
                <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
                  Sign in to your account
                </h2>
              </div>

              <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
                <form className="space-y-6" action="#" method="POST">
                  <div>
                    <label
                      htmlFor="email"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Email address
                    </label>
                    <div className="mt-2">
                      <input
                        id="email"
                        name="email"
                        type="email"
                        autoComplete="email"
                        required
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                      />
                    </div>
                  </div>

                  <div>
                    <div className="flex items-center justify-between">
                      <label
                        htmlFor="password"
                        className="block text-sm font-medium leading-6 text-gray-900"
                      >
                        Password
                      </label>
                      <div className="text-sm">
                        <a href="#" className="font-semibold text-[#357424] ">
                          Forgot password?
                        </a>
                      </div>
                    </div>
                    <div className="mt-2">
                      <input
                        id="password"
                        name="password"
                        type="password"
                        autoComplete="current-password"
                        required
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                      />
                    </div>
                  </div>

                  <div>
                    <button
                      type="submit"
                      className="flex w-full justify-center rounded-md bg-[green] px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-[#4ca54c9f] focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                      Sign in
                    </button>
                  </div>
                </form>

                <p className="mt-10 text-center text-sm text-gray-500">
                  Not a member?{" "}
                  <a
                    href="#"
                    className="font-semibold leading-6 text-[#263275]"
                  >
                    Start a 14 day free trial
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
